import { Component, OnInit, Inject } from '@angular/core';
import { ThietBiReqModel } from '../../../../../../shared/models/thiet-bi.model';
import { NoiQuanLyModel } from '../../../../../../shared/models/noi-quan-ly.model';
import { NuocSanXuatModel } from '../../../../../../shared/models/nuoc-san-xuat.model';
import {
  MatDialogRef,
  MAT_DIALOG_DATA,
  MatDialog,
} from '@angular/material/dialog';
import { UpdateThietBiService } from './update-thiet-bi.service';
import { AlertDialogComponent } from '../../../../../../shared/pages/alert-dialog/alert-dialog.component';
import { MatSnackBar } from '@angular/material/snack-bar';
import { TinhTrangModel } from 'src/app/shared/models/tinh-trang.model';
import { Router } from '@angular/router';
import { environment } from 'src/environments/environment';
@Component({
  selector: 'app-update-thiet-bi',
  templateUrl: './update-thiet-bi.component.html',
  styleUrls: ['./update-thiet-bi.component.scss'],
})
export class UpdateThietBiComponent implements OnInit {
  _thietBi = new ThietBiReqModel('', '', 1);
  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogMainRef: MatDialogRef<UpdateThietBiComponent>,
    private __service: UpdateThietBiService,
    private _alertDialog: MatDialog,
    private __snackbar: MatSnackBar,
    private __router: Router
  ) {}
  _title = 'Thêm tài sản';
  _titleMainButton = 'Thêm';
  showProgressBar = false;
  ngOnInit(): void {
    if (this.data) {
      if (this.data.thietBi) {
        this._title = 'Cập nhật';
        this._titleMainButton = 'Cập nhật';
        this._thietBi = this.data.thietBi;
      }
    }
  }

  update() {
    this.showProgressBar = true;
    this.__service.update(this._thietBi).subscribe(
      (data) => {
        this.showProgressBar = false;
        if (data === 'true') {
          this.__snackbar.open('Cập nhật thành công', null, {
            duration: 2000,
          });
          let res = this._thietBi;
          this.dialogMainRef.close({
            data: this._thietBi,
            result: true,
          });
        } else {
          this.__snackbar.open(data.Message, null, {
            duration: 2000,
          });
        }
      },
      (error) => {
        this.dialogMainRef.close({
          result: false,
        });
        this.showLogin();
        this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
          duration: 2000,
        });
      }
    );
  }
  showLogin() {
    this.__router.navigate(['admin/login']);
  }
  cancel() {
    var dialogRef = this._alertDialog.open(AlertDialogComponent, {
      disableClose: true,
      width: '17rem',
      data: {
        dataKey: 'Thay đổi của bạn sẽ không được lưu. Bạn có muốn hủy?',
        isConfirm: true,
      },
    });
    dialogRef.afterClosed().subscribe((res) => {
      if (res) {
        this.dialogMainRef.close({
          data: this._thietBi,
          result: false,
        });
      }
    });
  }
}
