import { Component, OnInit, ViewChild, Inject } from '@angular/core';
import {
  animate,
  state,
  style,
  transition,
  trigger,
} from '@angular/animations';
import {
  MatDialog,
  MatDialogRef,
  MAT_DIALOG_DATA,
} from '@angular/material/dialog';
import { ThietBiModel } from '../../../../../../shared/models/thiet-bi.model';
import { NoiQuanLyModel } from '../../../../../../shared/models/noi-quan-ly.model';
import { NuocSanXuatModel } from '../../../../../../shared/models/nuoc-san-xuat.model';
import { MatTableDataSource } from '@angular/material/table';
import { MatSort } from '@angular/material/sort';
import { MatPaginator } from '@angular/material/paginator';
import { QuanLyThietBiChiTietService } from './quan-ly-thiet-bi-chi-tiet.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ThietBiChiTietModel } from '../../../../../../shared/models/thiet-bi-chi-tiet.model';
import { UpdateThietBiChiTietComponent } from './update-thiet-bi-chi-tiet/update-thiet-bi-chi-tiet.component';
import { AlertDialogComponent } from 'src/app/shared/pages/alert-dialog/alert-dialog.component';
import { Router } from '@angular/router';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-quan-ly-thiet-bi-chi-tiet',
  templateUrl: './quan-ly-thiet-bi-chi-tiet.component.html',
  styleUrls: ['./quan-ly-thiet-bi-chi-tiet.component.scss'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({ height: '0', minHeight: '0' })),
      state('expanded', style({ height: '*' })),
      transition(
        'expanded <=> collapsed',
        animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')
      ),
    ]),
  ],
})
export class QuanLyThietBiChiTietComponent implements OnInit {
  _length = 0;
  displayedColumns = [
    'MaThietBiChiTiet',
    'TenNoiQuanLy',
    'TenNuocSanXuat',
    'NamSuDung',
    'TenTinhTrang',
    'SuDungToiDa',
    'HienThi',
    'actions',
  ];

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,

    public dialogMainRef: MatDialogRef<QuanLyThietBiChiTietComponent>,
    public __dialog: MatDialog,
    private __service: QuanLyThietBiChiTietService,
    private __snackbar: MatSnackBar,
    private __router: Router
  ) {}
  _nuocSanXuats: NuocSanXuatModel[];
  _noiQuanLys: NoiQuanLyModel[];
  _thietBiChiTiets: ThietBiChiTietModel[];
  dataSource: MatTableDataSource<ThietBiChiTietModel>;
  _thietBi: ThietBiModel;
  showProgressBar = false;
  ngOnInit(): void {
    if (this.data) {
      if (this.data.thietBi) {
        this._thietBi = this.data.thietBi;
      }
      if (
        this.data.noiQuanLys &&
        this.data.nuocSanXuats
        // &&
        // this.data.tinhTrangs
      ) {
        this._noiQuanLys = this.data.noiQuanLys;
        this._nuocSanXuats = this.data.nuocSanXuats;
      }
    }
    this.getDataThietBi();
  }
  private getDataThietBi() {
    //Lấy dữ liệu từ server

    this.showProgressBar = true;
    this.__service.get(this._thietBi.Ma).subscribe(
      (data) => {
        this.showProgressBar = false;
        if(data.Message){
          this.__snackbar.open(data.Message, null, {
            duration: 2000,
          });
          return
        }
        this._thietBiChiTiets = data;
        this.dataSource = new MatTableDataSource(this._thietBiChiTiets);

        this._length = this.dataSource.data.length;
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
        this.dataSource.filterPredicate = (
          data: ThietBiChiTietModel,
          filter: string
        ) =>
          data.TenNoiQuanLy.toLocaleLowerCase().indexOf(filter) != -1 ||
          data.TenTinhTrang.toLocaleLowerCase().indexOf(filter) != -1;

      },
      (error) => {
        this.dialogMainRef.close({
          result: false,
        });
        this.showLogin();
        this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
          duration: 2000,
        });
      }
    );
  }
  applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // Datasource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }
  themThietBi() {} //end add
  edit(thietBi: ThietBiModel) {
    const dialogRef = this.__dialog.open(UpdateThietBiChiTietComponent, {
      disableClose: true,
      width: '40rem',
      data: {
        thietBi: this._thietBi,
        thietBiChiTiet: thietBi,
        nuocSanXuats: this._nuocSanXuats,
        noiQuanLys: this._noiQuanLys,
      },
    });

    dialogRef.afterClosed().subscribe((res) => {
      if (res.result && res.result == true) {
        this.getDataThietBi();
      }
    });
  }
  lock(input: ThietBiChiTietModel) {
    var dialogRef = this.__dialog.open(AlertDialogComponent, {
      disableClose: true,
      width: '17rem',
      data: {
        dataKey: `Xóa thiết bị ${input.MaThietBi}-${input.MaThietBiChiTiet}-${input.TenThietBi}?`,
        isConfirm: true,
      },
    });
    dialogRef.afterClosed().subscribe((res) => {
      if (res) {
        this.showProgressBar = true;
        this.__service.lock(input).subscribe(
          (data) => {
            this.showProgressBar = false;
            if (data === 'true') {
              this.__snackbar.open('Xóa thành công', null, {
                duration: 2000,
              });
            } else {
              this.__snackbar.open(data, null, {
                duration: 2000,
              });
            }
          },
          (error) => {
            this.dialogMainRef.close({
              result: false,
            });
            this.showLogin();
            this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
              duration: 2000,
            });
          }
        );
      }
    });
  }
  unLock(input: ThietBiChiTietModel) {
    var dialogRef = this.__dialog.open(AlertDialogComponent, {
      disableClose: true,
      width: '17rem',
      data: {
        dataKey: `Khôi phục thiết bị ${input.MaThietBi}-${input.MaThietBiChiTiet}-${input.TenThietBi}?`,
        isConfirm: true,
      },
    });
    dialogRef.afterClosed().subscribe((res) => {
      if (res) {
        this.showProgressBar = true;
        this.__service.open(input).subscribe(
          (data) => {
            this.showProgressBar = false;
            if (data === 'true') {
              this.__snackbar.open('Cập nhật thành công', null, {
                duration: 2000,
              });
            } else {
              this.__snackbar.open(data, null, {
                duration: 2000,
              });
            }
          },
          (error) => {
            this.dialogMainRef.close({
              result: false,
            });
            this.showLogin();
            this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
              duration: 2000,
            });
          }
        );
      }
    });
  }
  showLogin() {
    this.__router.navigate(['admin/login']);
  }
  onSearchChange(searchText: String) {
    this.dataSource.filter = searchText.trim().toLowerCase();
  }
}
