import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../../../../environments/environment';
import { ThietBiModel } from './../../../../../shared/models/thiet-bi.model';

@Injectable({ providedIn: 'root' })
export class QuanLyThietBiService {
  constructor(private httpClient: HttpClient) {}

  getAll(): Observable<any> {
    let url = environment.api.url + 'ThietBi/getAll';
    return this.httpClient.get(url);
  }
  delete(thietBi: ThietBiModel): Observable<any> {
    let url = environment.api.url + 'ThietBi/delete';
    return this.httpClient.post(url, thietBi);
  }
  getAllNuocSanXuats(): Observable<any> {
    let url = environment.api.url + 'NuocSanXuat/getAll';
    return this.httpClient.get(url);
  }
  getAllNoiQuanLyes(): Observable<any> {
    let url = environment.api.url + 'NoiQuanLy/getAll';
    return this.httpClient.get(url);
  }
  getAllTinhTrangThietBi(): Observable<any> {
    let url = environment.api.url + 'TinhTrangThietBi/getAll';
    return this.httpClient.get(url);
  }
}
