import { Component, OnInit, Inject } from '@angular/core';
import { ThietBiReqModel } from '../../../../../../shared/models/thiet-bi.model';
import { NoiQuanLyModel } from '../../../../../../shared/models/noi-quan-ly.model';
import { NuocSanXuatModel } from '../../../../../../shared/models/nuoc-san-xuat.model';
import {
  MatDialogRef,
  MAT_DIALOG_DATA,
  MatDialog,
} from '@angular/material/dialog';
import { AddThietBiService } from './add-thiet-bi.service';
import { AlertDialogComponent } from '../../../../../../shared/pages/alert-dialog/alert-dialog.component';
import { MatSnackBar } from '@angular/material/snack-bar';
import { TinhTrangModel } from '../../../../../../shared/models/tinh-trang.model';
import { Router } from '@angular/router';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-add-thiet-bi',
  templateUrl: './add-thiet-bi.component.html',
  styleUrls: ['./add-thiet-bi.component.scss'],
})
export class AddThietBiComponent implements OnInit {
  _thietBi = new ThietBiReqModel('', '', 1);
  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogMainRef: MatDialogRef<AddThietBiComponent>,
    private __service: AddThietBiService,
    private _alertDialog: MatDialog,
    private __router: Router,
    private __snackbar: MatSnackBar
  ) {}
  _title = 'Thêm tài sản';
  _titleMainButton = 'Thêm';
  _nuocSanXuats: NuocSanXuatModel[];
  _noiQuanLys: NoiQuanLyModel[];
  __tinhTrangs: TinhTrangModel[];
  showProgressBar = false;
  ngOnInit(): void {
    if (this.data)
      if (this.data.thietBi) {
        this._title = 'Cập nhật';
        this._titleMainButton = 'Cập nhật';
        this._thietBi = this.data.thietBi;
      }
    if (
      this.data.noiQuanLys &&
      this.data.nuocSanXuats &&
      this.data.tinhTrangs
    ) {
      this._noiQuanLys = this.data.noiQuanLys;
      this._nuocSanXuats = this.data.nuocSanXuats;
      this.__tinhTrangs = this.data.tinhTrangs;
    }
  }

  add() {
    this.showProgressBar = true;
    this.__service.add(this._thietBi).subscribe(
      (data) => {
        this.showProgressBar = false;
        if (data === 'true') {
          this.__snackbar.open(`Thêm thiết bị ${this._thietBi.MaThietBi}-${this._thietBi.Ten} thành công`, null, {
            duration: 2000,
          });
          this.dialogMainRef.close({
            data: this._thietBi,
            result: true,
          });
        } else {
          this.__snackbar.open(data.Message, null, {
            duration: 2000,
          });
        }
      },
      (error) => {
        this.dialogMainRef.close({
          result: false,
        });
        this.showLogin();
        this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
          duration: 2000,
        });
      }
    );
  }
  showLogin() {
    this.__router.navigate(['admin/login']);
  }
  cancel() {
    var dialogRef = this._alertDialog.open(AlertDialogComponent, {
      disableClose: true,
      width: '17rem',
      data: {
        dataKey: 'Thay đổi của bạn sẽ không được lưu. Bạn có muốn hủy?',
        isConfirm: true,
      },
    });
    dialogRef.afterClosed().subscribe((res) => {
      if (res) {
        this.dialogMainRef.close({
          data: this._thietBi,
          result: false,
        });
      }
    });
  }
}
