import { Component, OnInit, ViewChild } from '@angular/core';
import {
  animate,
  state,
  style,
  transition,
  trigger,
} from '@angular/animations';
import { MatDialog } from '@angular/material/dialog';
import { AddThietBiComponent } from './add-thiet-bi/add-thiet-bi.component';
import { ThietBiModel } from '../../../../../shared/models/thiet-bi.model';

import { NoiQuanLyModel } from '../../../../../shared/models/noi-quan-ly.model';
import { NuocSanXuatModel } from '../../../../../shared/models/nuoc-san-xuat.model';
import { TinhTrangModel } from '../../../../../shared/models/tinh-trang.model';
import { MatTableDataSource } from '@angular/material/table';
import { MatSort } from '@angular/material/sort';
import { MatPaginator } from '@angular/material/paginator';
import { QuanLyThietBiService } from './quan-ly-thiet-bi.service';
import { AlertDialogComponent } from '../../../../../shared/pages/alert-dialog/alert-dialog.component';
import { MatSnackBar } from '@angular/material/snack-bar';
import { QuanLyThietBiChiTietComponent } from './quan-ly-thiet-bi-chi-tiet/quan-ly-thiet-bi-chi-tiet.component';
import { UpdateThietBiComponent } from './update-thiet-bi/update-thiet-bi.component';
import { environment, eMenuAdmin } from 'src/environments/environment';
import { MainAdminComponent } from '../../main-nav.component';
import { Router } from '@angular/router';

@Component({
  selector: 'app-quan-ly-thiet-bi',
  templateUrl: './quan-ly-thiet-bi.component.html',
  styleUrls: ['./quan-ly-thiet-bi.component.scss'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({ height: '0', minHeight: '0' })),
      state('expanded', style({ height: '*' })),
      transition(
        'expanded <=> collapsed',
        animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')
      ),
    ]),
  ],
})
export class QuanLyThietBiComponent implements OnInit {
  _length = 0;
  displayedColumns = ['Ma', 'Ten', 'SoLuong', 'TrangThai', 'actions'];

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  constructor(
    public dialog: MatDialog,
    private __service: QuanLyThietBiService,
    private __snackbar: MatSnackBar,
    private __router: Router,
    private __mainComponent: MainAdminComponent
  ) {
    localStorage.setItem(
      environment.key_storage.MENU.ADMIN,
      eMenuAdmin[eMenuAdmin.QUAN_LY_THIET_BI]
    );
    this.__mainComponent.__localStorage = localStorage.getItem(
      environment.key_storage.MENU.ADMIN
    );
  }
  _nuocSanXuats: NuocSanXuatModel[];
  _noiQuanLys: NoiQuanLyModel[];
  __tinhTrangs: TinhTrangModel[];
  _thietBis: ThietBiModel[];
  dataSource: MatTableDataSource<ThietBiModel>;
  showProgressBar = true;
  ngOnInit(): void {
    this.getDataNoiQuanLy();
    this.getDataNuocSanXuat();
    this.getDataThietBi();
    this.getDataTinhTrang();
  }
  showLogin() {
    this.__router.navigate(['admin/login']);
  }
  private getDataThietBi() {
    this.showProgressBar = true;
    this.__service.getAll().subscribe(
      (data) => {
        this.showProgressBar = false;
        if (data.Message) {
          this.__snackbar.open(data.Message, null, {
            duration: 2000,
          });
          return;
        }
        this._thietBis = data
        this.dataSource = new MatTableDataSource(this._thietBis);

        this._length = this.dataSource.data.length;
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
        this.dataSource.filterPredicate = (
          // tslint:disable-next-line:no-shadowed-variable
          data: ThietBiModel,
          filter: string
        ) =>
          data.Ma.toLocaleLowerCase().indexOf(filter) != -1 ||
          data.Ten.toLocaleLowerCase().indexOf(filter) != -1;
      },
      (error) => {
        this.showProgressBar = false;
        this.showLogin();
        this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
          duration: 2000,
        });
      }
    );
  }
  private getDataTinhTrang() {
    this.showProgressBar = true;
    this.__service.getAllTinhTrangThietBi().subscribe(
      (data) => {
        this.showProgressBar = false;
        if (data.Message) {
          this.__snackbar.open(data.Message, null, {
            duration: 2000,
          });
          return;
        }
        this.__tinhTrangs = data;
      },
      (error) => {
        this.showProgressBar = false;
        this.showLogin();
        this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
          duration: 2000,
        });
      }
    );
  }
  private getDataNuocSanXuat() {
    this.showProgressBar = true;
    this.__service.getAllNuocSanXuats().subscribe(
      (data) => {
        this.showProgressBar = false;
        if (data.Message) {
          this.__snackbar.open(data.Message, null, {
            duration: 2000,
          });
          return;
        }
        this._nuocSanXuats = data;
        this._nuocSanXuats = this._nuocSanXuats.filter((f) => f.HienThi);
      },
      (error) => {
        this.showProgressBar = false;
        this.showLogin();
        this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
          duration: 2000,
        });
      }
    );
  }
  private getDataNoiQuanLy() {
    this.showProgressBar = true;
    this.__service.getAllNoiQuanLyes().subscribe(
      (data) => {
        this.showProgressBar = false;
        if (data.Message) {
          this.__snackbar.open(data.Message, null, {
            duration: 2000,
          });
          return;
        }
        this._noiQuanLys = data;
        this._noiQuanLys = this._noiQuanLys.filter((f) => f.HienThi);
      },
      (error) => {
        this.showProgressBar = false;
        this.showLogin();
        this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
          duration: 2000,
        });
      }
    );
  }
  applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // Datasource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }
  themThietBi() {
    const dialogRef = this.dialog.open(AddThietBiComponent, {
      disableClose: true,
      width: '40rem',
      data: {
        noiQuanLys: this._noiQuanLys,
        nuocSanXuats: this._nuocSanXuats,

        tinhTrangs: this.__tinhTrangs,
      },
    });

    dialogRef.afterClosed().subscribe((res) => {
      if (res.result && res.result == true) {
        this.reload();
      }
    });
  } //end add
  reload() {
    this.getDataThietBi();
  }
  viewDetail(thietBi: ThietBiModel) {
    let dialogRef = this.dialog.open(QuanLyThietBiChiTietComponent, {
      disableClose: true,
      width: '90%',
      height: '99%',
      maxHeight: '99%',
      maxWidth: '90%',
      data: {
        thietBi: thietBi,
        noiQuanLys: this._noiQuanLys,
        nuocSanXuats: this._nuocSanXuats,
      },
    });
  }
  edit(thietBi: ThietBiModel) {
    const dialogRef = this.dialog.open(UpdateThietBiComponent, {
      disableClose: true,
      width: '40rem',
      data: {
        thietBi: thietBi,
      },
    });

    dialogRef.afterClosed().subscribe((res) => {
      if (res.result && res.result == true) {
        this.reload();
      }
    });
  }
  delete(thietBi: ThietBiModel) {
    const confirmDialogRef = this.dialog.open(AlertDialogComponent, {
      disableClose: true,
      width: '17rem',
      data: {
        dataKey: `Bạn có chắc muốn xóa thiết bị ${thietBi.MaThietBi}-${thietBi.Ten} ?`,
        isConfirm: true,
      },
    });
    confirmDialogRef.afterClosed().subscribe((result) => {
      if (result === true) {
        this.showProgressBar = true;
        this.__service.delete(thietBi).subscribe(
          (data) => {
            this.showProgressBar = false;
            if (data === 'true') {
              this.getDataThietBi();

              this.__snackbar.open('Đã xóa thiết bị', null, {
                duration: 2000,
              });
            } else {
              this.__snackbar.open(data, null, {
                duration: 2000,
              });
            }
          },
          (error) => {
            this.showProgressBar = false;
            this.showLogin();
            this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
              duration: 2000,
            });
          }
        );
      }
    });
  }

  onSearchChange(searchText: String) {
    this.dataSource.filter = searchText.trim().toLowerCase();
  }
}
