import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../../../../../../environments/environment';
import { ThietBiChiTietModel } from '../../../../../../../shared/models/thiet-bi-chi-tiet.model';

@Injectable({ providedIn: 'root' })
export class UpdateThietBiChiTietService {
  constructor(private httpClient: HttpClient) {}

  update(thietBi: ThietBiChiTietModel): Observable<any> {
    let url = environment.api.url + 'ThietBiChiTiet/update';
    return this.httpClient.post(url, thietBi);
  }

}
