import { Component, Inject, OnInit } from '@angular/core';
import {
  MAT_DIALOG_DATA,
  MatDialogRef,
  MatDialog,
} from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { NoiQuanLyModel } from 'src/app/shared/models/noi-quan-ly.model';
import { NuocSanXuatModel } from 'src/app/shared/models/nuoc-san-xuat.model';
import { ThietBiChiTietModel } from 'src/app/shared/models/thiet-bi-chi-tiet.model';
import {
  ThietBiModel,
  ThietBiReqModel,
} from 'src/app/shared/models/thiet-bi.model';
import { AlertDialogComponent } from 'src/app/shared/pages/alert-dialog/alert-dialog.component';
import { environment } from 'src/environments/environment';
import { UpdateThietBiChiTietService } from './update-thiet-bi-chi-tiet.service';

@Component({
  selector: 'app-update-thiet-bi-chi-tiet',
  templateUrl: './update-thiet-bi-chi-tiet.component.html',
  styleUrls: ['./update-thiet-bi-chi-tiet.component.scss'],
})
export class UpdateThietBiChiTietComponent implements OnInit {
  _thietBiChiTiet: ThietBiChiTietModel;
  _thietBi: ThietBiModel;
  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogMainRef: MatDialogRef<UpdateThietBiChiTietComponent>,
    private __service: UpdateThietBiChiTietService,
    private __router: Router,
    private _alertDialog: MatDialog,
    private __snackbar: MatSnackBar
  ) {}
  _title = 'Thêm tài sản';
  _titleMainButton = 'Thêm';
  showProgressBar = false;
  _nuocSanXuats: NuocSanXuatModel[];
  _noiQuanLys: NoiQuanLyModel[];
  ngOnInit(): void {
    if (this.data) {
      if (
        this.data.thietBiChiTiet &&
        this.data.nuocSanXuats &&
        this.data.noiQuanLys &&
        this.data.thietBi
      ) {
        this._title = 'Cập nhật';
        this._titleMainButton = 'Cập nhật';
        this._thietBiChiTiet = this.data.thietBiChiTiet;
        this._nuocSanXuats = this.data.nuocSanXuats;
        this._noiQuanLys = this.data.noiQuanLys;
        this._thietBi = this.data.thietBi;
      }
    }
  }

  update() {
    this.showProgressBar = true;
    this.__service.update(this._thietBiChiTiet).subscribe(
      (data) => {
        this.showProgressBar = false;
        if (data === 'true') {
          this.__snackbar.open('Cập nhật thành công', null, {
            duration: 2000,
          });
          let res = this._thietBiChiTiet;
          this.dialogMainRef.close({
            data: this._thietBiChiTiet,
            result: true,
          });
        } else {
          this.__snackbar.open(data.Message, null, {
            duration: 2000,
          });
        }
      },
      (error) => {
        this.dialogMainRef.close({
          result: false,
        });
        this.showLogin();
        this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
          duration: 2000,
        });
      }
    );
  }
  cancel() {
    var dialogRef = this._alertDialog.open(AlertDialogComponent, {
      disableClose: true,
      width: '17rem',
      data: {
        dataKey: 'Thay đổi của bạn sẽ không được lưu. Bạn có muốn hủy?',
        isConfirm: true,
      },
    });
    dialogRef.afterClosed().subscribe((res) => {
      if (res) {
        this.dialogMainRef.close({
          data: this._thietBiChiTiet,
          result: false,
        });
      }
    });
  }
  showLogin() {
    this.__router.navigate(['admin/login']);
  }
}
