import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../../../../../environments/environment';
import { ThietBiChiTietModel } from 'src/app/shared/models/thiet-bi-chi-tiet.model';

@Injectable({ providedIn: 'root' })
export class QuanLyThietBiChiTietService {
  constructor(private httpClient: HttpClient) {}
  get(maThietBi: string): Observable<any> {
    let url = environment.api.url + 'ThietBiChiTiet/get?MaThietBi=' + maThietBi;
    return this.httpClient.get(url);
  }
  lock(input: ThietBiChiTietModel): Observable<any> {
    input.HienThi = false;
    let url = environment.api.url + 'ThietBiChiTiet/update';
    return this.httpClient.post(url, input);
  }
  open(input: ThietBiChiTietModel): Observable<any> {
    input.HienThi = true;
    let url = environment.api.url + 'ThietBiChiTiet/update';
    return this.httpClient.post(url, input);
  }
}
